import React from "react";
import { Popover, Calendar } from "@faharmony/components";
import { readableDateTimeFormat } from "@faharmony/locale";
import { faCalendarAlt } from "@faharmony/icons";
import { TextField } from "../TextField";
import { styled } from "@faharmony/theme";

const StyledDatePicker = styled.div`
  overflow: auto;
  max-width: 260px;
  min-width: 180px;
`;

interface IDatePickerProps {
  name?: string;
  defaultValue?: Date;
  disabledDates?: Date[];
}

const isSameDate = (d1: Date, d2: Date) =>
  d1.getDate() === d2.getDate() &&
  d1.getMonth() === d2.getMonth() &&
  d1.getFullYear() === d2.getFullYear()
    ? true
    : false;

const useDatePicker = ({ defaultValue }: IDatePickerProps) => {
  const [state, setState] = React.useState<{ date?: Date; label: string }>({
    date: defaultValue,
    label: defaultValue ? readableDateTimeFormat(defaultValue, "date") : "",
  });

  const handleCalendarChange = React.useCallback(
    (date: Date, fn?: Function) => {
      setState({
        date,
        label: date ? readableDateTimeFormat(date, "date") : "",
      });
      fn && fn();
    },
    []
  );

  const handleInputChange = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const iVal = e.target.value;
      console.log("s", iVal);
      const parsedDate = Date.parse(iVal);
      setState((s) => ({
        label: iVal,
        date: parsedDate ? new Date(parsedDate) : s.date,
      }));
    },
    []
  );

  return {
    value: state.date,
    label: state.label,
    handleCalendarChange,
    handleInputChange,
  };
};

/**
 * DatePicker
 * ---
 * Description of component
 * @jira https://fasolutions.jira.com/browse/HAR-74
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <DatePicker />
 */
const DatePicker = React.forwardRef<HTMLInputElement, IDatePickerProps>(
  (props, ref) => {
    const { disabledDates = [], name } = props;
    const isDateDisabled = (date: Date) =>
      disabledDates.some((dd) => isSameDate(dd, date));

    const {
      value,
      handleCalendarChange,
      label,
      handleInputChange,
    } = useDatePicker(props);

    return (
      <Popover
        placement="bottom-end"
        content={({ closePopover }) => (
          <StyledDatePicker>
            <Calendar
              onChange={(date) => handleCalendarChange(date, closePopover)}
              value={value}
              disabledDates={disabledDates}
            />
          </StyledDatePicker>
        )}
      >
        <TextField
          ref={ref}
          name={name}
          prefixIcon={faCalendarAlt}
          placeholder={"YYYY-MM-DD"}
          value={label}
          onChange={handleInputChange}
          error={value && isDateDisabled(value) && "Invalid date"}
          readOnly
        />
      </Popover>
    );
  }
);

export { DatePicker, IDatePickerProps };
