import React from "react";
import { Icon, Tooltip } from "@faharmony/components";
import {
  //   faTimesCircle,
  faExclamationCircle,
  faCheckCircle,
  faCircleNotch,
} from "@faharmony/icons";
import { theme } from "@faharmony/theme";

import { IInputFieldProps, _IInputFieldContainerProps } from "./types";
import { StyledInputField, commonClass } from "./style";

/**
 * InputField
 * ---
 * Base Input component.
 * @jira https://fasolutions.jira.com/browse/HAR-ticker
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <InputField />
 */
const InputField = React.forwardRef<HTMLInputElement, IInputFieldProps>(
  (props, ref) => {
    let {
      isClearable = true,
      disabled = false,
      readOnly = false,
      spacing = "md",
      className = commonClass,
      onInputClear,
      placeholder = "Enter value...",
      prefixIcon,
      prefixText,
      isLoading,
      error,
      success,
      autoComplete = "off",
      textAlign = "left",
      ...inputProps
    } = props;
    // Set values while loading
    if (isLoading) {
      error = undefined;
      success = undefined;
      readOnly = true;
    }
    const statusTooltip = isLoading ? "Loading" : error || success;

    const inputField = (
      <input
        {...inputProps}
        className={className + " input"}
        disabled={disabled}
        ref={ref}
        readOnly={readOnly}
        placeholder={placeholder}
        autoComplete={autoComplete}
        style={{ textAlign }}
      />
    );
    // Field prefix
    const prefixComponent = (prefixIcon || prefixText) && (
      <div className={className + " prefix"}>
        {prefixIcon ? <Icon icon={prefixIcon} /> : prefixText || ""}
      </div>
    );

    // Field suffix
    const suffixComponent = statusTooltip && (
      <div className={className + " suffix"}>
        {isLoading && (
          <Icon
            icon={faCircleNotch}
            color={theme.color.text.disabled}
            size="lg"
            spin
          />
        )}
        {error && (
          <Icon icon={faExclamationCircle} color={theme.color.tint.red} />
        )}
        {success && (
          <Icon icon={faCheckCircle} color={theme.color.tint.green} />
        )}
      </div>
    );
    const suffixComponentWithTooltip = typeof statusTooltip === "string" &&
      suffixComponent && (
        <Tooltip value={statusTooltip || ""}>{suffixComponent}</Tooltip>
      );

    const showClearButton = isClearable && !disabled && !readOnly;
    //   isClearVisible;

    return (
      <StyledInputField
        className={className + " inputContainer"}
        isClearable={showClearButton}
        disabled={disabled}
        spacing={spacing}
        readonly={readOnly}
        prefixIcon={prefixIcon}
        prefixText={prefixText}
        error={error}
      >
        {inputField}
        {prefixComponent}
        {suffixComponentWithTooltip || suffixComponent}
      </StyledInputField>
    );
  }
);

export { InputField, IInputFieldProps };
